
        let contents = `:host{align-self:stretch;display:grid;display:flex;align-items:center;gap:12px;grid-template:"icon label badge" minmax(56px,min-content) / 24px 1fr minmax(0,min-content);justify-items:flex-start;padding-inline:16px 24px}#icon{grid-area:icon}#slot{display:block;text-align:start}:host([active]){color:rgb(var(--mdw-ink))}#shape{max-inline-size:none;grid-column:auto;grid-row:1 / 2}#badge-text{z-index:1}`;
        let sheet;
        try {
          sheet = new CSSStyleSheet();
          sheet.replaceSync(contents);
        } catch (e) {
          const doc = document.implementation.createHTMLDocument()
          const style = doc.createElement('style');
          style.textContent = contents;
          doc.head.append(style);
          sheet = style.sheet;
          // Note: Removing style from document will nullify sheet
        }
        export default sheet;