
        let contents = `:host{grid-auto-flow:row;grid-auto-rows:minmax(20px,min-content);grid-template-rows:[icon] minmax(32px,1fr);grid-template-columns:[icon] minmax(56px,1fr);padding-inline:12px}#slot{display:contents;overflow-wrap:anywhere;word-break:break-all;word-break:break-word}#badge{max-inline-size:40px;grid-column:1 / 2}`;
        let sheet;
        try {
          sheet = new CSSStyleSheet();
          sheet.replaceSync(contents);
        } catch (e) {
          const doc = document.implementation.createHTMLDocument()
          const style = doc.createElement('style');
          style.textContent = contents;
          doc.head.append(style);
          sheet = style.sheet;
          // Note: Removing style from document will nullify sheet
        }
        export default sheet;